.iphone {
	position: relative;
	width: 220px;

	@include media (w1024) {
		width: 280px;
	}

	@include media(w768) {
		width: 140px;
	}

	&__img {
		width: 100%;
	}

	&__content {
		background-color: red;
		position: absolute;
		top: 11.5%;
		left: 6%;
		height: 76%;
		width: 88%;
	}
}